import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { useInView } from 'react-intersection-observer';

import contactTel from '../images/contact-tel.jpg';
import contactMail from '../images/contact-mail.jpg';
import { PHONE, EMAIL } from '../../const';

const H2 = (props: any) => {
  const { children, sub }: { children: React.ReactNode; sub: string } = props;

  return (
    <Typography
      variant='h2'
      mr={1}
      p={0}
      lineHeight={1.5}
      sx={{
        '&::before': {
          content: `"${sub}"`,
          display: 'block',
          color: 'primary.main',
          mb: -1,
          top: 0,
          left: 0,
          fontSize: '1rem',
          letterSpacing: 2,
          lineHeight: 1
        }
      }}
    >
      {children}
    </Typography>
  );
};

const H3 = ({
  children,
  bold = false,
  noLine = false
}: {
  children: React.ReactNode;
  bold?: boolean;
  noLine?: boolean;
}) => (
  <Typography
    variant='h3'
    fontWeight={bold ? 'bold' : 'normal'}
    sx={{
      display: 'inline-block',
      position: 'relative',
      zIndex: 0,
      '::after': noLine
        ? {}
        : {
            display: 'inline-block',
            position: 'absolute',
            content: `''`,
            left: 0,
            bottom: -2,
            height: 6,
            width: '100%',
            zIndex: -1,
            backgroundColor: 'primary.main'
          }
    }}
  >
    {children}
  </Typography>
);

const Transition = ({
  children,
  delay = 0
}: {
  children: React.ReactNode;
  delay?: number;
}) => {
  const { ref, inView } = useInView({
    // 画面の上下中央を inview の基準線にする
    rootMargin: '-25% 0px',
    threshold: 0,
    triggerOnce: true
  });

  return (
    <Box
      ref={ref}
      sx={{
        width: '100%',
        height: '100%'
      }}
    >
      <Box
        sx={{
          width: '100%',
          height: '100%',
          transition: [
            `opacity 1000ms ease-out ${delay}ms`,
            `transform 1000ms ease-out ${delay}ms`
          ].join(','),
          transform: `translateX(${inView ? 0 : 100}px)`,
          opacity: inView ? 1 : 0
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

interface IContactValues {
  name: string;
  tel: string;
  email: string;
  inquiry: string;
}

export const Contact = () => {
  const [isSubmitted, setIsSubmitted] = React.useState<boolean>(false);
  const [isValidContact, setIsValidContact] = React.useState<boolean>(false);
  const [contactValues, setContactValues] = React.useState<IContactValues>({
    name: '',
    tel: '',
    email: '',
    inquiry: ''
  });

  React.useEffect(() => {
    setIsValidContact(
      !!(
        contactValues.name &&
        contactValues.tel &&
        contactValues.email &&
        contactValues.inquiry
      )
    );
  }, [contactValues]);

  return (
    <Box
      id='contact'
      sx={{
        '.MuiInputBase-input': {
          color: 'text.primary'
        }
      }}
    >
      <Transition>
        <H2 sub='CONTACT'>お問い合わせ</H2>
      </Transition>
      <Typography mb={2}>
        転職⽀援のご依頼やサービスについてのご相談など、お気軽にお問い合わせください。
      </Typography>

      <Box
        maxWidth='lg'
        bgcolor='background.paper'
        mb={10}
        p={{ sm: 8, xs: 2 }}
      >
        <Box maxWidth='md' mx='auto'>
          <Box>
            {isSubmitted ? (
              <Typography>
                お問い合わせいただきありがとうございます。
                <br />
                のちほど、担当者よりご連絡させていただきます。
              </Typography>
            ) : (
              <form
                // @TODO
                // action='https://docs.google.com/forms/u/2/d/e/1FAIpQLSe8CzvXXBH4uGupLTYRbHrEHWyV_IS6uHWriy6Hh_24_fL8PA/formResponse'
                method='post'
                target='onSubmitIframe'
              >
                {[
                  {
                    id: 'name',
                    type: 'text',
                    name: 'entry.85566551',
                    label: 'お名前（個⼈名・法⼈名）'
                  },
                  {
                    id: 'tel',
                    type: 'tel',
                    name: 'entry.688651344',
                    label: '電話番号',
                    required: false
                  },
                  {
                    id: 'email',
                    type: 'email',
                    name: 'entry.126843600',
                    label: 'メールアドレス'
                  }
                ].map(
                  ({
                    required = true,
                    id,
                    type,
                    name,
                    label
                  }: {
                    required?: boolean;
                    id: string;
                    type: string;
                    name: string;
                    label: string;
                  }) => (
                    <TextField
                      required={required}
                      key={id}
                      id={id}
                      type={type}
                      name={name}
                      label={label}
                      variant='outlined'
                      value={(contactValues as any)[id]}
                      fullWidth
                      sx={{
                        my: 2,
                        '& .MuiOutlinedInput-root': {
                          '& fieldset': {
                            borderRadius: '0px'
                          }
                        }
                      }}
                      onChange={e =>
                        setContactValues({
                          ...contactValues,
                          [id]: e.target.value
                        })
                      }
                    />
                  )
                )}
                <TextField
                  required
                  id='inquiry'
                  name='entry.427845865'
                  label='お問い合わせ内容'
                  variant='outlined'
                  multiline
                  rows={6}
                  fullWidth
                  sx={{
                    my: 2,
                    '& .MuiOutlinedInput-root': {
                      '& fieldset': {
                        borderRadius: '0px'
                      }
                    }
                  }}
                  onChange={e =>
                    setContactValues({
                      ...contactValues,
                      inquiry: e.target.value
                    })
                  }
                />
                <Button
                  disabled={!isValidContact}
                  type='submit'
                  variant='contained'
                  size='large'
                  disableElevation={true}
                  sx={{
                    display: 'block',
                    mx: 'auto',
                    my: 2,
                    px: 4,
                    borderRadius: '0px'
                  }}
                >
                  送信
                </Button>
              </form>
            )}
            <iframe
              title='iframe'
              name='onSubmitIframe'
              style={{ display: 'none' }}
              onLoad={() => {
                setIsSubmitted(true);
              }}
            />
          </Box>
        </Box>
      </Box>
      <Grid container>
        {[
          {
            image: contactTel,
            note: (
              <>
                <H3>お電話でのお問い合わせ</H3>
                <br />
                <Link href={`tel:${PHONE}`} color='text.primary'>
                  {PHONE}
                </Link>
                <br />
                営業時間：08:00-17:00
              </>
            )
          },
          {
            image: contactMail,
            note: (
              <>
                <H3>メールでのお問い合わせ</H3>
                <br />
                <Link href={`mailto:${EMAIL}`} color='text.primary'>
                  {EMAIL}
                </Link>
                <br />
                営業時間：24時間
              </>
            )
          }
        ].map(({ image, note }, i) => (
          <Grid item key={i} md={6} sm={12} xs={12}>
            <Box
              position='relative'
              sx={{
                width: '100%',
                height: {
                  lg: 360,
                  md: 300,
                  sm: 240,
                  xs: 180
                },
                backgroundImage: `url(${image})`,
                backgroundPosition: 'center center',
                backgroundSize: 'cover'
              }}
            >
              <Box
                position='absolute'
                display='flex'
                justifyContent='center'
                alignItems='center'
                width='100%'
                height='100%'
                bgcolor='rgba(0,0,0,0.5)'
              >
                <Box
                  sx={{
                    p: {
                      sm: 4,
                      xs: 1
                    },
                    backgroundColor: 'rgba(255,255,255,0.9)'
                  }}
                >
                  {note}
                </Box>
              </Box>
            </Box>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};
